<template>
  <div class="operation">
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      fixed
      @click-left="onClickLeft"
    />

    <van-tabs swipeable animated sticky title-active-color="#DDA944" @change="tabChange">
      <van-tab title="全部">
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

          <van-radio-group v-model="radio" v-if="list.length > 0">
            <van-radio v-for="item in list" :name="item.applyId" checked-color="#DDA944">
              <div class="name">{{item.applyUnit||'--'}}</div>
              <div class="msg">{{item.applyPhone||'--'}}</div>
              <div class="msg">{{item.applyReason||'--'}}</div>
              <div class="msg">{{item.applyTime||'--'}}</div>
              <img :src="stateImgPath[item.auditState]" alt="">
            </van-radio>
          </van-radio-group>
          <div class="no_data" v-else>
            <img src="../../../assets/images/mall/nodata.png" alt="">
            <div class="no_data_msg">暂无数据</div>
          </div>

        </van-pull-refresh>
      </van-tab>
      <van-tab title="未审批">
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

          <van-radio-group v-model="radio" v-if="stateLength['0'] > 1">
            <van-radio v-for="item in list" v-if="item.auditState === 0" :name="item.applyId" checked-color="#DDA944">
              <div class="name">{{item.applyUnit||'--'}}</div>
              <div class="msg">{{item.applyPhone||'--'}}</div>
              <div class="msg">{{item.applyReason||'--'}}</div>
              <div class="msg">{{item.applyTime||'--'}}</div>
              <img :src="stateImgPath[item.auditState]" alt="">
            </van-radio>
          </van-radio-group>
          <div class="no_data" v-else>
            <img src="../../../assets/images/mall/nodata.png" alt="">
            <div class="no_data_msg">暂无数据</div>
          </div>

        </van-pull-refresh>
      </van-tab>
      <van-tab title="已授权">
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

          <van-radio-group v-model="radio" v-if="stateLength['1'] > 0">
            <van-radio v-for="item in list" v-if="item.auditState === 1" :name="item.applyId" checked-color="#DDA944">
              <div class="name">{{item.applyUnit||'--'}}</div>
              <div class="msg">{{item.applyPhone||'--'}}</div>
              <div class="msg">{{item.applyReason||'--'}}</div>
              <div class="msg">{{item.applyTime||'--'}}</div>
              <img :src="stateImgPath[item.auditState]" alt="">
            </van-radio>
          </van-radio-group>
          <div class="no_data" v-else>
            <img src="../../../assets/images/mall/nodata.png" alt="">
            <div class="no_data_msg">暂无数据</div>
          </div>

        </van-pull-refresh>
      </van-tab>
      <van-tab title="未通过">
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

          <van-radio-group v-model="radio" v-if="stateLength['2'] > 0">
            <van-radio v-for="item in list" v-if="item.auditState === 2" :name="item.applyId" checked-color="#DDA944">
              <div class="name">{{item.applyUnit||'--'}}</div>
              <div class="msg">{{item.applyPhone||'--'}}</div>
              <div class="msg">{{item.applyReason||'--'}}</div>
              <div class="msg">{{item.applyTime||'--'}}</div>
              <img :src="stateImgPath[item.auditState]" alt="">
            </van-radio>
          </van-radio-group>
          <div class="no_data" v-else>
            <img src="../../../assets/images/mall/nodata.png" alt="">
            <div class="no_data_msg">暂无数据</div>
          </div>

        </van-pull-refresh>
      </van-tab>
    </van-tabs>

    <div class="foot">
      <div class="foot_btn" @click="submit(1)">通过</div>
      <div class="foot_btn" @click="submit(2)">不通过</div>
      <div class="foot_btn" @click="submit(0)">取消</div>
    </div>

  </div>
</template>

<script>
  export default {
    name: "operation",
    data() {
      return {
        radio: 1,
        title: '运营管理',
        token: '',
        isLoading: false,
        tabIndex: 0,
        list: [],
        stateLength:{
          0:0,
          1:0,
          2:0
        },
        stateImgPath:{
          0: require('../../../assets/images/mall/verify.png'),
          1: require('../../../assets/images/mall/success.png'),
          2: require('../../../assets/images/mall/failure.png'),
        },
        submitStates: {
          0: '取消',
          1: '通过',
          2: '不通过',
        }
      }
    },
    methods: {
      onClickLeft() {
        this.$router.back();
      },
      tabChange(index) {
        this.radio = 1;
      },
      onRefresh() {
        this.getData(()=>{
          this.$toast('刷新成功');
          this.isLoading = false;
        })
      },
      submit(auditState){
        if (this.radio === 1) {
          this.$toast('请选择一行数据');
        } else {
          this.$dialog.confirm({
            title: '友情提示',
            message: '确定要设置当前权限为【' + this.submitStates[auditState] + '】？',
            cancelButtonText: '我再想想'
          }).then(() => {

            // let item;
            // for (let i = 0; i < this.list.length; i++) {
            //   if(this.list[i].applyId === this.radio){
            //     item = this.list[i];
            //   }
            // }
            //
            this.$http.post(backstageIp + userUrl + '/getFlagshipData',
              {
                url: cfaeIp + '/mall/audit/updateAudit.json',
                type: 'post',
                cookie: 'token=' + this.token,
                formData: JSON.stringify({
                  applyId: this.radio,
                  auditState: auditState,
                  // userId: item.userId
                })
              }
            ).then(res => {
              console.log(res);
            }).catch(err => {
              this.$toast('当前无网络连接或网络不稳定，请稍后重试');
            });
          }).catch(err => {

          });
        }
      },
      getData(fn){
        this.$http.post(backstageIp + userUrl + '/getFlagshipData',
          {
            url: cfaeIp + '/mall/audit/getAuditList.json?flag=1&pageNum=1&pageSize=10000',
            type: 'get',
            cookie: 'token=' + this.token
          }
        ).then(res => {
          if (res.data.success) {
            let data = JSON.parse(res.data.data).data.result;
            for (let i = 0; i < data.length; i++) {
              // data[i].checked = false;
              this.stateLength[data[i].auditState]++;
            }
            this.list = data;
            console.log(data);
            fn && fn();
          } else {
            this.$toast('当前无网络连接或网络不稳定，请稍后重试');
          }
        }).catch(err => {
          this.$toast('当前无网络连接或网络不稳定，请稍后重试');
        });
      }
    },
    created() {
      this.token = sessionStorage.getItem('token');
      this.getData();
    }
  }
</script>

<style scoped>

  >>> .van-tabs__wrap {
    margin-top: 46px;
    height: 40px;
  }
  >>> .van-tab {
    line-height: 40px;
    font-size: 15.5px;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: rgba(124, 124, 125, 1);
  }
  >>> .van-tabs__line {
    bottom: 21.5px;
    background: rgba(221, 169, 68, 1);
    border-radius: 1.5px;
  }

  .van-pull-refresh {
    margin: 40px 0 64px;
    background: rgba(247, 248, 250, 1);
  }
  >>>.van-pull-refresh__track {
    min-height: calc(100vh - 150px);
    overflow: hidden;
  }
  .foot {
    position: fixed;
    bottom: 0;
    height: 64px;
    width: 100%;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px -1px 5px 0px rgba(0, 0, 0, 0.16);
  }
  .foot .foot_btn {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 75px;
    height: 35px;
    background: rgba(221, 169, 68, 1);
    box-shadow: 0px 6px 13.5px 0px rgba(221, 169, 68, 0.5);
    border-radius: 28px;
    font-size: 17px;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: rgba(255, 255, 255, 1);
    line-height: 35px;
    text-align: center;
  }
  .foot .foot_btn:nth-child(1) {right: 185px;}
  .foot .foot_btn:nth-child(2) {right: 100px;}
  .foot .foot_btn:nth-child(3) {right: 15px;}

  .van-radio {
    height: 110px;
    line-height: 110px;
    background-color: #ffffff;
    margin-top: 10px;
    position: relative;
  }
  >>>.van-radio .van-radio__icon {
    margin-left: 15px;
  }
  .van-radio:last-child {
    margin-bottom: 10px;
  }
  .van-radio .name {
    position: absolute;
    top: 17px;
    left: 45px;
    right: 100px;
    height: 20px;
    font-size: 14px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(32, 32, 32, 1);
    line-height: 20px;
    verflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .van-radio .msg {
    position: absolute;
    left: 45px;
    right: 100px;
    height: 16.5px;
    font-size:12px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(155,155,155,1);
    line-height:16.5px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .van-radio .msg:nth-child(2) {top: 45.5px;}
  .van-radio .msg:nth-child(3) {top: 62px;}
  .van-radio .msg:nth-child(4) {top: 78.5px;}
  .van-radio img {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 15px;
    width: 70px;
    height: 61.5px;
    margin: auto;
  }
  .no_data img {
    position: absolute;
    height: 105px;
    top: 189.5px;
    left: 0;
    right: 0;
    margin: auto;
    -webkit-transform: translate(10%, 0);
    -moz-transform: translate(10%, 0);
    -ms-transform: translate(10%, 0);
    -o-transform: translate(10%, 0);
    transform: translate(10%, 0);
  }
  .no_data .no_data_msg {
    position: absolute;
    top: 301.5px;
    width: 100%;
    font-size: 15.5px;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: rgba(167, 167, 167, 1);
    line-height: 22px;
    letter-spacing: 1px;
    text-align: center;
  }

</style>



